import React from "react";
import { Card, CardHeader, CardTitle, CardBody } from "reactstrap";
import Swiper from "react-id-swiper";
import img1 from "../../assets/img/slider/banner-1.jpg";
import img2 from "../../assets/img/slider/banner-2.jpg";
import img3 from "../../assets/img/slider/banner-3.jpg";
import img4 from "../../assets/img/slider/banner-4.jpg";
import img5 from "../../assets/img/slider/banner-5.jpg";
import img6 from "../../assets/img/slider/banner-6.jpg";
import img7 from "../../assets/img/slider/banner-7.jpg";
import img8 from "../../assets/img/slider/banner-8.jpg";
import img9 from "../../assets/img/slider/banner-9.jpg";
import img10 from "../../assets/img/slider/banner-10.jpg";


const params = {
  spaceBetween: 200,
  centeredSlides: true,
  autoplay: {
    delay: 2500,
    disableOnInteraction: true,
  },
  pagination: {
    el: ".swiper-pagination",
    clickable: true,
  },
  
};

if(window.screen.width > 500 ) {
  params.navigation = {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  };
};

class Banner extends React.Component {
  render() {
    return (
      <div>
      <Swiper {...params}>
        <div >
          <img
            src={img1}
            alt="swiper 1"
            className="img-fluid"
            style={{ minHeight: "230px",marginBottom: '10px' }}
          />
        </div>
        <div>
          <img
            src={img2}
            alt="swiper 2"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img3}
            alt="swiper 3"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img4}
            alt="swiper 4"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img5}
            alt="swiper 5"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img6}
            alt="swiper 5"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img7}
            alt="swiper 5"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img8}
            alt="swiper 5"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img9}
            alt="swiper 5"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
        <div>
          <img
            src={img10}
            alt="swiper 5"
            className="img-fluid"
            style={{ minHeight: "230px" }}
          />
        </div>
      </Swiper>
      </div>
    );
  }
}
export default Banner;
